.item {
    padding: 137px 0px 0px 0px;

    &__border {
        padding: 32px;
        border: 1px solid #E5E5E5;
    }

    &__row {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__title {
        font-weight: 700;
        font-size: 24px;
        line-height: 133%;
    }

    &__buttons {
        display: flex;
        font-size: 0;
        border: 1px solid #E5E5E5;
    }

    &__btn {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 6px 15px;
        &:not(:last-child) {
            border-right: 1px solid #E5E5E5;
        }
        & > button {
            color: #9C9C9C;
            background-color: #fff;
            font-size: 14px;
            line-height: 143%;
            letter-spacing: 0.25px;
        }
        & > button.active {
            color: #333333;
            font-weight: 700;
        }
    }

    &__diagram {
        width: 100%;
        height: 230px;
        margin: 0px 0px 20px 0px;
    }

    &__total {
        margin: 0px 0px 22px 0px;
    }

    &__total-title {
        font-size: 12px;
        letter-spacing: 0.4px;
        line-height: 133%;
        color: #9C9C9C;
    }

    &__total-text {
        font-weight: 700;
        font-size: 24px;
        line-height: 133%;
    }

}

.bottom-item {
    display: flex;
    align-items: center;

    &__size {
        & >:not(:last-child) {
            margin: 0px 32px 0px 0px;
        }
    }

    &__title {
        font-size: 12px;
        letter-spacing: 0.4px;
        line-height: 133%;
        color: #9C9C9C;
    }

    &__text {
        color: #333333;
        font-weight: 700;
        font-size: 18px;
        line-height: 133%;
    }
}

@media (max-width: 768px){
    .item {
        padding: 50px 0px 0px 0px;
    
        &__border {
            padding: 15px;
        }
    
        &__row {
            margin: 0px 0px 10px 0px;
        }

        &__title {
            font-size: 20px;
            margin: 0px 5px 0px 0px;
        }
    
    
        &__btn {
            padding: 6px 10px;
            & > button {
                font-size: 12px;
                letter-spacing: 0px;
            }
        }
    
        &__diagram {    
            margin: 0px 0px 20px 0px;
        }
    
    
        &__total-text {
            font-size: 20px;
        }
    
    }
    
    .bottom-item {
    
        &__size {
            & >:not(:last-child) {
                margin: 0px 25px 0px 0px;
            }
        }
    
        &__text {
            font-size: 16px;
        }
    }
}